<template>
  <!-- -->
  <!---->
    
  <HostHeader />
  <div class="wrap">
    <AppMenu />
    <!-- <div>
            <div class="clearfix cover20">
                <div class="preview-image">
                    <img class="img20" :src="previewImage">
                </div>

                <div>
                    <p class="p20"></p>
                </div>

                <div class="input">
                    <input type="file" id="profile" name="profile" class="file-input" style="font-size: 20px;"
                        @change="handleImageChange">
                </div>
            </div>
        </div> -->
    <!---->
    <div class="companyJoinTitleJ">
      <h1 class="companyModifyName">{{ this.companyVo.companyName }}</h1>
      <div class="companyJoinImg">
        <div class="preview-image">
          <img v-bind:src="previewImage" style="width: 180px; height: 180px; border-radius: 10px" />
        </div>

        <div class="input">
          <input type="file" id="profile" name="profile" class="file-input" style="width: auto"
            @change="handleImageChange" />
        </div>
      </div>
      <div class="companyJoinInfo">
        <div>
          <!-- <label for="companyNo"></label> -->
          <input type="text" name="" id="businesNumber" v-model="companyVo.companyBn" placeholder="" disabled />
          <!-- <input type="button" value=" " v-on:click="businessNumber" /> -->
          <!-- <p v-if="isBn"></p>
          <p v-else-if="isBn == null"> .</p>
          <p v-else> </p> -->
        </div>
        <div>
          <!-- <label for="companyName"></label> -->
          <input type="text" name="" id="companyName" v-model="companyVo.companyName" placeholder="" />
        </div>
        <div>
          <!-- <label for="companyName"></label> -->
          <textarea type="text" id="companyDescription" v-model="companyVo.companyIntro" placeholder="">
          </textarea>
        </div>
        <div>
          <!-- <label for="companyId"></label> -->
          <input type="text" name="" id="companyId" v-model="companyVo.companyId" placeholder="" disabled />
        </div>
        <div>
          <!-- <label for="companyPass"></label> -->
          <input type="password" name="" id="companyPass" placeholder=" " v-model="companyPassChack" minlength="8"
            maxlength="16" />
          <p> .(8~16)</p>
        </div>
        <div>
          <!-- <label for="companyPassChack"></label> -->
          <input type="password" name="" id="companyPassChack" placeholder=" " v-model="companyVo.companyPass" />
          <p v-if="companyPassChack != '' &&
        companyPassChack == companyVo.companyPass
        ">
             
          </p>
          <p v-else>  .</p>
        </div>
        <div class="companyAddress">
          <input type="text" id="companyZipCode" placeholder="" v-model="companyVo.companyZipCode" readonly />
          <input type="button" v-on:click.prevent="DaumPostcode()" value=" " /><br />
          <input type="text" id="companyNameAddress" placeholder="" v-model="companyVo.companyNameAddress"
            readonly />
          <input type="text" id="companyNumAddress" placeholder="" v-model="companyVo.companyNumAddress" readonly />
          <span id="guide" style="color: #999; display: none"></span>
          <input type="text" id="companyDetailAddress" placeholder="" v-model="companyVo.companyDetailAddress" />
        </div>
        <div>
          <!-- <label for="companyPassChack"></label> -->
          <input
            type="text"
            name=""
            id="companyHp"
            placeholder=" "
            v-model="companyVo.companyHp"
          />
        </div>
      </div>
      <div class="companyJoinButton">
        <button><router-link to="/companypage"></router-link></button>
        <button v-on:click.prevent="modify"></button>
      </div>
      <!--
        <div id="map">
        
        </div>
        -->
    </div>
  </div>
  <AppFooter />
</template>
<script>
import HostHeader from "@/components/HostAppHeader.vue";
import AppFooter from "@/components/AppFooter.vue";
import AppMenu from "@/components/CompanyMenu.vue";
import "@/assets/css/jh/jh.css";
import "@/assets/css/Initialization.css";
// import Swal from 'sweetalert2';

import axios from "axios";

export default {
  components: {
    HostHeader,
    AppFooter,
    AppMenu,
  },
  data() {
    return {
      previewImage: "",
      isBn: null,
      isId: null,
      isHp: null,
      //
      companyPassChack: "",
      companyVo: {
        companyBn: "",
        companyName: "",
        companyIntro: "",
        companyId: "",
        companyPass: "",
        companyHp: "",
        companyImage: "",
        //
        companyZipCode: "",
        companyNameAddress: "",
        companyNumAddress: "",
        companyDetailAddress: "",
        companyLatitude: "", //
        companyLongitude: "", //
      },
      file2: "",
      message: "",
      accessToken:
        "EAAN3krJLRh4BOygZACSanLiJZBMg51rLvXr7z0qYo0vJoUAHh090BzGVB02229haKuaqulu9MiG64ERZCFojgoFJ4OHe5QXOszYXRrqsZB7Al4yY7HKV7An7PC3R3rLjEXb0QQg1CTCXZA5uN9SZAtB3sLBRoTa4VT7pU74KBZBEP4lEkZAMl6hK0IsjYfwKufg7", //   
      pageId: "320016071194453", //   ID
    };
  },

  methods: {
    modify() {
      const formData = new FormData();

      if (this.file2 == "") {
        // console.log(' ');
      } else {
        formData.append("companyFile", this.file2);
      }

      formData.append("companyBn", this.companyVo.companyBn);
      formData.append("companyName", this.companyVo.companyName);
      formData.append("companyIntro", this.companyVo.companyIntro);
      formData.append("companyId", this.companyVo.companyId);
      formData.append("companyPass", this.companyVo.companyPass);
      formData.append("companyHp", this.companyVo.companyHp);
      //formData.append("companyFile", this.companyVo.companyImage); //
      formData.append("companyImage", this.companyVo.companyImage);
      formData.append("companyZipCode", this.companyVo.companyZipCode);
      formData.append("companyNameAddress", this.companyVo.companyNameAddress);
      formData.append("companyNumAddress", this.companyVo.companyNumAddress);
      formData.append(
        "companyDetailAddress",
        this.companyVo.companyDetailAddress
      );
      formData.append("companyLatitude", this.companyVo.companyLatitude);
      formData.append("companyLongitude", this.companyVo.companyLongitude);

      if (this.companyVo.companyBn == "") {
        alert("  ");
      } else if (this.companyVo.companyName == "") {
        alert("  ");
      } else if (this.companyVo.companyIntro == "") {
        alert("  ");
      } else if (this.companyVo.companyId == "") {
        alert("  ");
      } else if (this.companyPassChack == "") {
        alert("  ");
      } else if (this.companyVo.companyPass == null) {
        alert("  ");
      } else if (this.companyVo.zonecode == "") {
        alert("  ");
      } else if (this.companyVo.detailAddress == "") {
        alert("  ");
      } else if (this.companyVo.companyHp == "") {
        alert("  ");
      } else {
        // console.log(this.companyVo);
        axios({
          method: "put",
          url: `${this.$store.state.apiBaseUrl}/odo/companymodify`,
          headers: {
            "Content-Type": "multipart/form-data",
            Authorization: "Bearer " + this.$store.state.cToken,
          }, //
          data: formData,
          responseType: "json",
        })
          /* eslint-disable */
          .then((response) => {
            if (response.data.result == "success") {
              alert(".");
              let authCompany = response.data.apiData;
              this.$store.commit("setAuthCompany", authCompany);
              this.$router.push("/companypage");
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
    businessNumber() {
      //console.log("businessNumber");
      const data = { b_no: [this.companyVo.companyBn] };
      axios
        .post(
          "https://api.odcloud.kr/api/nts-businessman/v1/status?serviceKey=kLwu8It5iiIWVEWui%2FFpNx7qI2XcPU6H6lfgnHJ1RGVI0nNAR9yfRk7eWA8m9ncjMV%2FSeJ2g36xCarutBsixGw%3D%3D",
          data,
          {
            headers: {
              "Content-Type": "application/json",
              Authorization:
                "Bearer GBoS9ElaWUq6vtcfzi1g5xvqS0tBleTdBxI7d6Sal2YAjx+TVLTIID3c+ul1j0bzkUn/AqoXjbXoKI0YZbe7wg==",
            },
          }
        )
        .then((response) => {
          //console.log("Response:", response.data);

          if (
            response.data &&
            response.data.data &&
            response.data.data.length > 0
          ) {
            const valid = response.data.data[0].b_stt_cd;
            if (valid === "01") {
              this.isBn = true;
            } else {
              this.isBn = false;
            }
          } else {
            this.isBn = false;
          }
        })
        .catch((error) => {
          console.error("Error:", error);
          this.isBn = false;
        });
    },

    DaumPostcode() {
      new window.daum.Postcode({
        oncomplete: (data) => {
          // 
          this.companyVo.companyZipCode = data.zonecode;
          this.companyVo.companyNameAddress = data.roadAddress;
          this.companyVo.companyNumAddress = data.jibunAddress;
          // // ,  
          var geocoder = new window.kakao.maps.services.Geocoder();
          geocoder.addressSearch(
            this.companyVo.companyNameAddress,
            (result, status) => {
              if (status === window.kakao.maps.services.Status.OK) {
                //     
                this.companyVo.companyLatitude = result[0].y; // 
                this.companyVo.companyLongitude = result[0].x; // 
                // console.log(
                //   ":",
                //   this.companyVo.companyLatitude,
                //   ":",
                //   this.companyVo.companyLongitude
                // );
              } else {
                //   
                console.error("  ");
              }
            }
          );
        },
      }).open();
    },

    handleImageChange(event) {
      //  
      this.profile = event.target.files[0];
      this.file2 = event.target.files[0];
      //console.log(this.companyVo.companyImage)
      // FileReader    
      const reader = new FileReader();

      //      
      reader.onload = (e) => {
        //    previewImage    
        this.previewImage = e.target.result;
      };

      //  
      if (this.profile) {
        reader.readAsDataURL(this.profile);
      }
    },

    map() {
      const script = document.createElement("script");
      script.src =
        "https://openapi.map.naver.com/openapi/v3/maps.js?ncpClientId=z8bkhu391h";

      script.async = true;

      script.defer = true;
      document.head.appendChild(script);

      script.onload = () => {
        //   
        new window.naver.maps.Map("map", {
          center: new window.naver.maps.LatLng(37.5112, 127.0981),
          zoom: 13,
          mapTypeControl: true,
        });
        new window.naver.maps.Marker({
          //   
          position: new window.naver.maps.LatLng(37.5112, 127.0981),
          map: new window.naver.maps.Map("map", {
            center: new window.naver.maps.LatLng(37.5112, 127.0981),
            zoom: 13,
            mapTypeControl: true,
          }),
        });
      };
    },
    list() {
      axios({
        method: "get", // put, post, delete
        url: `${this.$store.state.apiBaseUrl}/odo/companymodify`,
        headers: {
          "Content-Type": "application/json; charset=utf-8",
          Authorization: "Bearer " + this.$store.state.cToken,
        }, //
        //params: guestbookVo, //get   
        //data: this.$store.state.authUser.userNo, //put, post, delete   JSON  
        responseType: "json", //
      })
        .then((response) => {
          //console.log(response); //
          this.companyVo = response.data.apiData;
          // console.log(this.companyVo);
          this.previewImage = `${this.$store.state.apiBaseUrl}/upload/${this.companyVo.companyImage}`;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  mounted() {
    //   API 
  },

  created() {
    //this.map();
    this.list();
  },
};
</script>

<style></style>
