<template>
  <!-- -->

  <!---->

  <AppHeader />
  <div class="wrap">
    <div class="paymentBoxAll">
      <p>/ </p>
      <div class="paymentBox">
        <div class="paymentClassBox clearfix">
          <img v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${this.pv.classImage}`" alt="" />
          <div class="paymentTitle">
            <p>{{ this.pv.className }}</p>
            <p>{{ this.pv.classIntro }}</p>
            <p>{{ this.pv.companyName }}</p>
            <div v-if="this.pv.endDate == null" class="paymentTitle">
              <p>: <span>{{ this.pv.startDate }}</span></p>
            </div>
            <div v-else class="paymentTitle">
              <p>: <span>{{ this.pv.startDate }}</span> ~ <span>{{ this.pv.endDate }}</span></p>
            </div>
          </div>
        </div>
      </div>

      <div class="paymentContentBox">
        <p></p>
        <form action="">
          <ul class="paymentType">
            <li>
              <div>
                <input type="radio" style="width: 15px; border: 1px" name="pay" v-bind:value="''"
                  v-model="paymenType" checked="checked" />
                <label></label>
              </div>
              <!-- <select name="" id="">
                <option value="" disabled selected> </option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select> -->
            </li>
            <li>
              <input type="radio" style="width: 15px; border: 1px" name="pay" v-bind:value="'naver'"
                v-model="paymenType" />
              <label></label>
            </li>
            <li>
              <input type="radio" style="width: 15px; border: 1px" name="pay" v-bind:value="'toss'"
                v-model="paymenType" />
              <label></label>
            </li>
            <li>
              <input type="radio" style="width: 15px; border: 1px" name="pay" v-bind:value="''"
                v-model="paymenType" />
              <label></label>
            </li>
          </ul>
          <div class="paymentCoupon">
            <div>
              <div class="couponSelect">
                <p></p>
                <button v-on:click.prevent="listup"></button>
              </div>
              <p v-if="a == ''">{{ this.a }}</p>
              <p v-else>{{ Number(this.pv.classPrice * this.a / 100).toLocaleString("ko-KR") }} </p>
            </div>
            <div>
              <p class="couponText" v-if="this.couponList.length > 0">{{ this.couponList.length }}    
              </p>
              <p class="couponText" v-else>  </p>
            </div>
          </div>
        </form>
      </div>
      <div class="paymentPay">
        <div class="paymentContent">
          <p></p>
          <p></p>
          <p></p>
        </div>
        <div class="paymentResult">
          <p>{{ Number(this.pv.classPrice).toLocaleString("ko-KR") }}</p>
          <p v-if="a > 0">
            {{ this.a }}%({{ Number(this.pv.classPrice * this.a / 100).toLocaleString("ko-KR") }})
          </p>
          <p v-if="a == ''">{{ this.a }}</p>
          <p v-if="a != ''">{{ Number(this.pv.classPrice - this.pv.classPrice * this.a /
            100).toLocaleString("ko-KR") }}
            <span></span>
          </p>
          <p v-else>{{ Number(this.pv.classPrice).toLocaleString("ko-KR") }}</p>
        </div>
      </div>
      <div class="pamentButtonBox">
        <button><router-link to="/"></router-link></button>
        <button v-on:click="pay"></button>
      </div>
    </div>
  </div>

  <div id="addModal">
    <div class="modal-content2">
      <form action="">
        <p class="closeBtn" v-on:click="close">x</p>
        <label for="none"></label>
        <input  id="none" type="radio" name="coupon" value="" v-model="a" checked="checked"
          v-on:click="couponNo0" /><br><br>
        <div v-for="(coupon, i) in couponList" v-bind:key="i">
          <label :for="'coupon_' + i">{{ coupon.coupon_price }}% </label>
          <input :id="'coupon_' + i" type="radio" name="coupon" :value="coupon.coupon_price" v-model="a"
            @click="couponNo(coupon.coupon_no)" /><br><br>
        </div>
        <button v-on:click.prevent="close2"></button>
      </form>
    </div>
  </div>
  <AppFooter />
</template>
<script>
import axios from 'axios';

import AppHeader from "@/components/AppHeader.vue";
import AppFooter from "@/components/AppFooter.vue";
import "@/assets/css/jh/payment.css";
import "@/assets/css/Initialization.css";
import Swal from 'sweetalert2';

export default {
  components: {
    AppHeader,
    AppFooter,
  },
  data() {
    return {
      a: "",
      paymenType: "",
      pv: {
        classIntro: '',
        className: '',
        classPrice: '',
        companyName: '',
        companyNo: '',
        endDate: '',
        startDate: '',
        classImage: ''
      },
      couponList: [],
      total: '',
      payVo: {
        scheduleNo: '',
        payType: '',
        total: '',
        couponNo: ''
      }
    };
  },
  mounted() { },
  methods: {
    listup() {

      let viewModal = document.querySelector("#addModal");

      viewModal.style.display = "block";
    },
    close() {
      let viewModal = document.querySelector("#addModal");

      viewModal.style.display = "none";
    },
    close2() {
      let viewModal = document.querySelector("#addModal");

      viewModal.style.display = "none";
    },
    payType(type) {
      // console.log(type);
      if (type == "") {
        this.paymenType = "";
      } else if (type == "naver") {
        this.paymenType = "naver";
      } else if (type == "toss") {
        this.paymenType = "toss";
      } else if (type == "") {
        this.paymenType = "";
      }
    },
    list() {
      // console.log("");
      axios({
        method: 'get', // put, post, delete 
        url: `${this.$store.state.apiBaseUrl}/odo/payment/${this.$route.params.no}`,
        headers: { "Content-Type": "application/json; charset=utf-8", "Authorization": "Bearer " + this.$store.state.token }, //
        //params: guestbookVo, //get   
        //data: this.$store.state.authUser.userNo, //put, post, delete   JSON  
        responseType: 'json' //
      }).then(response => {
        if (response.data.message == "fail") {
          Swal.fire({
            title: " ",
            html:
              " "
          });
          this.$store.commit('setAuthUser', '');
          this.$store.commit('setToken', '');
          this.$router.push('/');
          //console.log(response)

        } else {
          // console.log(response); //
          this.pv = response.data.apiData.pv;
          this.couponList = response.data.apiData.couponList;
          //console.log(this.pv);
          //console.log(this.couponList);
        }
      }).catch(error => {
        console.log(error);
      });
    },
    pay() {
      //console.log(`${this.$route.params.no}`);
      //console.log(this.paymenType);
      if (this.a == "") {
        this.total = this.pv.classPrice
      } else {
        this.total = this.pv.classPrice - this.pv.classPrice * this.a / 100
      }
      //.log(this.total);
      //.log(this.$store.state.couponNo);
      this.payVo.scheduleNo = `${this.$route.params.no}`;
      this.payVo.payType = this.paymenType;
      this.payVo.total = this.total;
      this.payVo.couponNo = this.$store.state.couponNo;
      //console.log(this.payVo);
      axios({
        method: 'put', // put, post, delete 
        url: `${this.$store.state.apiBaseUrl}/odo/pay`,
        headers: { "Content-Type": "application/json; charset=utf-8", "Authorization": "Bearer " + this.$store.state.token }, //
        //params: guestbookVo, //get   
        data: this.payVo, //put, post, delete   JSON  
        responseType: 'json' //
      }).then(response => {
        // console.log(response.data.apiData.message); //
        if (response.data.apiData.message == "") {
          Swal.fire({
            title: " ",
            html:
              " "
          });
          this.$store.commit('setAuthUser', '');
          this.$store.commit('setToken', '');
          this.$router.push('/login/user');
          window.location.reload(true);
        } else {
          // console.log(response.data.apiData.apiData);

          this.$router.push('/paymentend/' + response.data.apiData.apiData);
        }

      }).catch(error => {
        console.log(error);
      });

    },
    couponNo(a) {
      this.$store.commit("setCouponNo", a);
    },
    couponNo0() {
      this.$store.commit("setCouponNo", 0);
    }
  },
  created() {
    this.list();
  },
};
</script>

<style></style>
