<template>
  <!-- -->

  <AppHeader />
  <!---->
  <div class="wrap">
    <div class="companyInfoBox">
      <h2></h2>
      <!---->


      <div class="companyInfo">
        <div class="companyInfotitle">
          <h2 class="companyTitle">{{ this.ci.companyName }}</h2>
          <div class="comapnyContent">
            <div class="infoImgBox">
              <img class="companyImg" v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${ci.companyImage}`" alt="" />

              <img v-if="this.ci.wishNo > 0" class="comapnyImg2" src="../../assets/images/redheart.svg" alt=""
                v-on:click="change(ci.wishNo)">
              <img v-else class="comapnyImg2" src="../../assets/images/whiteheart.svg" alt=""
                v-on:click="change(ci.wishNo)">

            </div>
            <div class="infContent">
              <div class="InfoTitle">
                <p></p>
                <p></p>
                <p></p>
              </div>
              <div class="infoContent">
                <p>{{ this.ci.companyHp }}</p>
                <p>{{ this.ci.companyAddress }}</p>
                <p>
                  {{ this.ci.companyIntro }}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- -->
      <div class="companyClass">
        <p> </p>
        <div class="classInfoTotalBox" v-for="(c, i) in ci2" v-bind:key="i">
          <div class="infoImg">
            <img v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${c.classImage}`" alt=""
              @click="goDetailPage(c.classNo)" />
            <img v-if="c.wishNo > 0" src="../../assets/images/redheart.svg" alt=""
              v-on:click="change2(c.wishNo, c.classNo, i)" />
            <img v-else-if="c.wishNo == 0" src="../../assets/images/whiteheart.svg" alt=""
              v-on:click="change2(c.wishNo, c.classNo, i)" />
          </div>
          <div class="flexBox">
            <div class="infoContentBox">
              <div class="">
                <p class="infoName">{{ c.className }}</p>
                <p class="infoIntro">{{ c.classIntro }}</p>
              </div>
              <div class="infoClassStar">
                <span></span>
                <p class="infoReview">{{ c.reviewPoint }}</p>
                <p class="infoReview">{{ c.reviewCount }} </p>
              </div>
            </div>
            <div class="classStarChart">
              <canvas :id="'chart-' + i" ref="charts" width="320px" height="200px" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <AppFooter />
</template>
<script>
import axios from 'axios';

import AppHeader from "@/components/AppHeader.vue";
import AppFooter from "@/components/AppFooter.vue";
import "@/assets/css/jh/companyinfo.css";
import "@/assets/css/Initialization.css";
import Swal from 'sweetalert2';

import { Chart, registerables } from "chart.js";
Chart.register(...registerables);
//import ChartDataLabels from "chartjs-plugin-datalabels";
//Chart.register(ChartDataLabels);

export default {
  components: {
    AppHeader,
    AppFooter,
  },
  data() {
    return {
      ci: {
        companyName: '',
        companyHp: '',
        companyAddress: '',
        companyIntro: '',
        companyImage: ''
      },
      ci2: [],
      data: false,
      img2: "",
      img: ""
    };
  },
  mounted() {
    //this.createCharts();
  },
  methods: {
    change2(wishNo, classNo, i) { //   
      if(this.$store.state.authUser == '' && this.$store.state.token == ''){
				Swal.fire({
					title: "   .",
					text: " ?",
					icon: "warning",
					showCancelButton: true,
					confirmButtonColor: "#3085d6",
					cancelButtonColor: "#d33",
					confirmButtonText: "",
					cancelButtonText: ""
				}).then(result => {
					if (result.isConfirmed) { //   confirm  
						// ...
						this.$router.push('/login/user');

					} else if (result.isDismissed) { //   cancel  
						// ...
					}
				});

      } else if (wishNo == 0) {
        axios({
          method: 'post', // put, post, delete 
          url: `${this.$store.state.apiBaseUrl}/odo/companyinfo/wishInsert`,
          headers: { "Content-Type": "application/json; charset=utf-8", "Authorization": "Bearer " + this.$store.state.token }, //
          //params: guestbookVo, //get   
          data: classNo, //put, post, delete   JSON  
          responseType: 'json' //
          /* eslint-disable */
        }).then(response => {
          // console.log(response); //
          if(response.data.message != "fail"){
            this.ci2[i].wishNo = 1;
          }else{
            this.ci2[i].wishNo = 0;
          }
          

        }).catch(error => {
          console.log(error);
        });
      } else {

        axios({
          method: 'delete', // put, post, delete 
          url: `${this.$store.state.apiBaseUrl}/odo/companyinfo/wishDelete`,
          headers: { "Content-Type": "application/json; charset=utf-8", "Authorization": "Bearer " + this.$store.state.token }, //
          //params: guestbookVo, //get   
          data: classNo, //put, post, delete   JSON  
          responseType: 'json' //
          /* eslint-disable */
        }).then(response => {
          // console.log(response); //

          this.ci2[i].wishNo = 0;

        }).catch(error => {
          console.log(error);
        });
      }
    },
    change(no) {  //   
      // console.log(no)
      if(this.$store.state.authUser == '' && this.$store.state.token == ''){
				Swal.fire({
					title: "   .",
					text: " ?",
					icon: "warning",
					showCancelButton: true,
					confirmButtonColor: "#3085d6",
					cancelButtonColor: "#d33",
					confirmButtonText: "",
					cancelButtonText: ""
				}).then(result => {
					if (result.isConfirmed) { //   confirm  
						// ...
						this.$router.push('/login/user');

					} else if (result.isDismissed) { //   cancel  
						// ...
					}
				});
      }else if (no > 0) {
        axios({
          method: 'delete', // put, post, delete 
          url: `${this.$store.state.apiBaseUrl}/odo/companyinfo/wishDelete2`,
          headers: { "Content-Type": "application/json; charset=utf-8", "Authorization": "Bearer " + this.$store.state.token }, //
          //params: guestbookVo, //get   
          data: no, //put, post, delete   JSON  
          responseType: 'json' //
          /* eslint-disable */
        }).then(response => {
          // console.log(response); //

          this.ci.wishNo = 0;

        }).catch(error => {
          console.log(error);
        });
      } else {
        axios({
          method: 'post', // put, post, delete 
          url: `${this.$store.state.apiBaseUrl}/odo/companyinfo/wishInsert2`,
          headers: { "Content-Type": "application/json; charset=utf-8", "Authorization": "Bearer " + this.$store.state.token }, //
          //params: guestbookVo, //get   
          data: `${this.$route.params.no}`, //put, post, delete   JSON  
          responseType: 'json' //
        }).then(response => {
          //console.log(response.data.apiData); //

          this.ci.wishNo = response.data.apiData;

        }).catch(error => {
          console.log(error);
        });
      }
    },
    goDetailPage(a) {
      //   .
      this.$router.push("/classdetailpage/" + a);
    },
    createCharts() {
      //   
      this.ci2.forEach((classData, index) => {
        const chartData = {
          type: 'bar',
          data: {
            labels: [' ', ' ', ' '], //  
            datasets: [
              {
                //label: `${classData.className} `,
                data: [classData.q1, classData.q2, classData.q3], // ci2   
                backgroundColor: [
                  'rgba(255, 99, 132, 0.2)',
                  'rgba(54, 162, 235, 0.2)',
                  'rgba(255, 206, 86, 0.2)',
                ],
                borderColor: [
                  'rgba(255, 99, 132, 1)',
                  'rgba(54, 162, 235, 1)',
                  'rgba(255, 206, 86, 1)',
                ],
                borderWidth: 1,
              },
            ],
          },

          options: {

            responsive: false,
            plugins: {
              legend: {
                display: false //   false  .
              },
              datalabels: {
                display: true,
                color: 'black',
              },
            },
          },

        };

        //  
        new Chart(this.$refs.charts[index], {
          type: chartData.type,
          data: chartData.data,
          options: chartData.options,
        });
      });
    },
    list() {
      axios({
        method: 'get', // put, post, delete 
        url: `${this.$store.state.apiBaseUrl}/odo/companyinfo/${this.$route.params.no}`,
        headers: { "Content-Type": "application/json; charset=utf-8", "Authorization": "Bearer " + this.$store.state.token }, //
        //params: guestbookVo, //get   
        //data: this.$store.state.authUser.userNo, //put, post, delete   JSON  
        responseType: 'json' //
      }).then(response => {
        // console.log(response); //
        this.ci = response.data.ci;
        this.ci2 = response.data.ci2;
        this.$nextTick(() => {
          this.createCharts();
        });

        // console.log(this.ci2);
      }).catch(error => {
        console.log(error);
      });
    }
  },
  created() {
    this.list();
  },
};
</script>

<style></style>
