<template>

	<AppHeader/>
	<!-- header -->
	
	<div class="login-join">
		<span class="lj-txt">{{ loginTitle }}</span>
		<div class="lj-form">

			<div class="select-join">
				<div class="join-box">
					<button type="button" @click="userJoin" class="join-select"> </button>
					<img class="kakao-login" @click=kakaoJoin src="@/assets/images/icon/ss/kakao_login_medium_narrow.png">
					<!-- <button type="button" @click.="naverJoin" class="join-select"> </button> -->
				</div>
			</div>

			<div class="user-join">
				<div class="join1">
					<div class="join-box">
						<div class="join1-1">
							<input id="join-id" type="text"  v-model="joinVo.userId" placeholder=""><br>
							<input id="join-pw" type="password" v-model="joinVo.userPw" placeholder="">
							<!-- <span class="join1-txt">/, ,   2   10</span> -->
							<input id="join-pw2" type="password" v-model="checkPw" placeholder="   "><br>
						</div>
					</div>
					<button class="login-btn" type="button" @click="showSelectJoin"> </button>
					<button class="login-btn" type="button" @click="showJoin2"></button>
				</div>
				<!-- join1 -->
				<div class="join2">
					<div class="join-box">
						<div class="join2-1">
							<input id="join-name" type="text" v-model="joinVo.userName" placeholder="">
							<input id="join-nickname" type="text" v-model="joinVo.userNickname" placeholder="">
							<input id="join-email" type="email" v-model="joinVo.userEmail" placeholder=""><br>
							<span v-if="joinVo.userEmail !== null && joinVo.userEmail !== '' && !isJoinIdValid" class="join-id-msg">   </span>
							<input id="join-hp" type="text" v-model="joinVo.userHp" :placeholder="`  '-'  `">
							<input id="join-birth" type="text" v-model="joinVo.userBirth" :placeholder="` '-'   ex)19980303`">
							<div class="join2-2">
								<label for="joinVoMale"></label>
								<input id="joinVoMale" type="radio" name="gender" v-model="joinVo.userGender" value="male">
								<label for="joinVoFemale"></label>
								<input id="joinVoFemale" type="radio" name="gender" v-model="joinVo.userGender" value="female">
							</div>
						</div>
						<button class="login-btn" v-on:click="showJoin1"></button>
						<button class="login-btn" type="submit" v-on:click.prevent="joinFinish"></button>
					</div>
				</div>
				<!-- join2 -->
				<div class="join3">
					<div class="join-box">
						<span>{{ joinVo.userNickname }} </span><br>
						<span> </span><br>
						<router-link to="/" class="login-btn"> </router-link>
						<router-link to="/login/user" class="login-btn"></router-link>
					</div>
				</div>
				<!-- join3 -->
			</div>
			<!-- user-join -->
		</div>
		<!-- lj-form -->
	</div>
	<!-- login-join -->

	<AppFooter/>
	<!-- footer -->
</template>




<script>
import '@/assets/css/Initialization.css';
import '@/assets/css/ss/login-join.css';
import AppHeader from '@/components/AppHeader.vue';
import AppFooter from '@/components/AppFooter.vue';
import axios from 'axios';
import Swal from 'sweetalert2';


export default {
	name: "JoinPage",
	components: {
		AppHeader,
		AppFooter
	},
	data() {
		return {
			check : false,
			checkPw : '',
			joinVo : {
				userId : '',
				userEmail : '',
				userPw : '',
				userName : '',
				userNickname : '',
				userHp : '',
				userBirth : '',
				userGender : ''
			},
			loginTitle : '!',
		};
	},
	computed :{
		//     
		isJoinIdValid(){
			return this.validateEmail(this.joinVo.userEmail);
		},
	},
	methods: {

		//    
		userJoin(){
			let select = document.querySelector('.select-join');
			let user = document.querySelector('.user-join');
			select.style.display = 'none';
			user.style.display = 'block';
			this.loginTitle = '';
		},

		//  
		kakaoJoin(){
			window.Kakao.Auth.authorize({
				redirectUri : `${this.$store.state.apiBaseUrl}/login/user`,
				prompt : 'login'
			})
		},
		
		//  
		naverJoin(){
			console.log('');
		},

		//     
		showSelectJoin(){
			this.joinVo = {
				userId: '',
				userPw: '',
				userNickname: '',
				userHp: '',
				userBirth: '',
				userGender: ''
			}
			this.checkPw = '';
			this.loginTitle = '!';
			let select = document.querySelector('.select-join');
			let user = document.querySelector('.user-join');
			select.style.display = 'block';
			user.style.display = 'none';
		},

		//   
		validateEmail(inputEmail) {
			const regex = /\S+@\S+\.\S+/;
			return regex.test(inputEmail);
		},

		//     -    +  
		/* eslint-disable */
		showJoin2(){
			let pw = this.joinVo.userPw;
			if(pw === null || pw === ''){
				Swal.fire({ text : ' ', icon : 'error', });
			} else if(pw.search(/\s/) != -1) {
				Swal.fire({ text : '  ', icon : 'error', });
			} else if (pw != this.checkPw){
				Swal.fire({text : ' ', icon : 'error',});
			} else {
				axios({
					method: 'get',
					url: `${this.$store.state.apiBaseUrl}/odo/ss/checkid`,
					headers: { 'Content-Type': 'application/json; charset=utf-8' },
					params : {id : this.joinVo.userId},
					responseType: 'json'
				}).then(response => {
					if(response.data.apiData == 1){
						let join1 = document.querySelector('.join1');
						let join2 = document.querySelector('.join2');
						join1.style.display = 'none';
						join2.style.display = 'block';
					} else if(response.data.apiData == -1) {
						Swal.fire({text : '  ', icon : 'error',});
					}
				}).catch(error => {
					console.log(error);
				});
			}
		},

		//    
		showJoin1(){
			let join1 = document.querySelector('.join1');
			let join2 = document.querySelector('.join2');
			join1.style.display = 'block';
			join2.style.display = 'none';
		},

		//    
		joinFinish (){
			let userName = this.joinVo.userName;
			let nickname = this.joinVo.userNickname;
			let hp = this.joinVo.userHp;
			let birth = this.joinVo.userBirth;
			let gender = this.joinVo.userGender;
			let email = this.joinVo.userEmail;

			if(userName === null || userName === ''){
				Swal.fire({text : ' ', icon : 'error',});
			} else if(userName.search(/\s/) != -1){
				Swal.fire({ text : '  ', icon : 'error', });
			} else if(nickname === null || nickname === ''){
				Swal.fire({text : ' ', icon : 'error',});
			} else if(nickname.search(/\s/) != -1){
				Swal.fire({ text : '  ', icon : 'error', });
			} else if(email === null || email === ''){
				Swal.fire({text : ' ', icon : 'error',});
			} else if(hp === null || hp === ''){
				Swal.fire({text : ' ', icon : 'error',});
			} else if(hp.search(/\s/) != -1){
				Swal.fire({ text : '  ', icon : 'error', });
			} else if(birth === null || birth === ''){
				Swal.fire({text : ' ', icon : 'error',});
			} else if(birth.search(/\s/) != -1){
				Swal.fire({ text : '  ', icon : 'error', });
			} else if(gender === null || gender === ''){
				Swal.fire({text : ' ', icon : 'error',});
			} else {
				axios({
					method: 'post',
					url: `${this.$store.state.apiBaseUrl}/odo/ss/userjoin`,
					headers: { 'Content-Type': 'application/json; charset=utf-8' },
					data: this.joinVo,
					responseType: 'json'
				}).then(response => {
					if(response.data.result === 'success'){
						this.loginTitle = '!'
						let join3 = document.querySelector('.join3');
						let join2 = document.querySelector('.join2');
						join2.style.display = 'none';
						join3.style.display = 'block';
					} else {
						Swal.fire({text: ' .', icon: 'error'});
					}
				}).catch(error => {
					console.log(error);
				});
			}
		}
	},
	created(){

	}
};
</script>





<style>
.swal-wide{
	width : 100px;
}

</style>