<template>
    <AppHeader />
    <div class="wrap">
      <div class="clearfix">
        <AppMenu />
        <div class="companyInfoClassList">
          <p>  </p>
        </div>
        <div class="photo-preview">
          <img :src="photoPreview || placeholderImage" alt=" " />
        </div>
        <input class="under" type="file" @change="handleFileUpload" />
        <div class="inputText">
          <input v-model="message" placeholder="   ex) !  !" />
          <button @click="postToFacebook" :disabled="loading"></button>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import AppMenu from "@/components/CompanyMenu.vue";
  import AppHeader from "@/components/HostAppHeader.vue";
  import Swal from 'sweetalert2';
  import axios from "axios";
  
  export default {
    name: "PostPacebook",
    components: { AppMenu, AppHeader },
    data() {
      return {
        message: "",
        photo: null,
        photoPreview: null,
        placeholderImage: require("../../assets/images/nope.png"),
        accessToken: "EAAN3krJLRh4BOygZACSanLiJZBMg51rLvXr7z0qYo0vJoUAHh090BzGVB02229haKuaqulu9MiG64ERZCFojgoFJ4OHe5QXOszYXRrqsZB7Al4yY7HKV7An7PC3R3rLjEXb0QQg1CTCXZA5uN9SZAtB3sLBRoTa4VT7pU74KBZBEP4lEkZAMl6hK0IsjYfwKufg7",
        pageId: "320016071194453",
        loading: false,
      };
    },
    methods: {
      handleFileUpload(event) {
        this.photo = event.target.files[0];
        if (this.photo != null) {
          this.photoPreview = URL.createObjectURL(this.photo);
        } else {
          this.photoPreview = null;
        }
      },
      postToFacebook() {
        if (this.photo) {
          this.uploadPhoto(); //    
        } else {
          this.createPost(); //       
        }
      },
      uploadPhoto() {
        this.loading = true; //   
        Swal.fire({
          title: "  ...",
          allowOutsideClick: false,
        });
  
        const formData = new FormData();
        formData.append("source", this.photo);
        formData.append("access_token", this.accessToken);
  
        const photoUrl = `https://graph.facebook.com/${this.pageId}/photos`;
  
        axios.post(photoUrl, formData)
          .then((response) => {
            // console.log(response)
            // console.log(response.data.id)
            const photoId = response.data.id;
            this.createPost(photoId); //      
          })
          .catch((error) => {
            this.loading = false; //   
            Swal.close();
            Swal.fire({
              title: "    ",
              icon: "error",
              text: error.message,
            });
            // console.error("     :", error);
          });
      },
      createPost(photoId = null) {
        const url = `https://graph.facebook.com/${this.pageId}/feed`;
        const params = {
          message: this.message,
          access_token: this.accessToken,
        };
        if (photoId) {
          params.object_attachment = photoId;
        }
  
        axios.post(url, null, { params })
        /* eslint-disable */
          .then((response) => {
            this.loading = false; //   
            Swal.close();
            Swal.fire({
              title: " ",
              icon: "success",
            });
            this.resetForm();
            // console.log(" :", response.data);
          })
          .catch((error) => {
            this.loading = false; //   
            Swal.close();
            Swal.fire({
              title: "   ",
              icon: "error",
              text: error.message,
            });
            // console.error("    :", error);
          });
      },
      resetForm() {
        this.message = "";
        this.photo = null;
        this.photoPreview = null;
      },
    },
  };
  </script>
  
  <style>
  .classChack {
    border-bottom: 2px solid #8521ff;
  }
  </style>