<template>
    <div>
        <!-- AppHeader -->
        <AppHeader />
        <div class="wrap">
            <!-- <MyPageHeader /> -->
            <div>
                <!-- <MyPageSide /> -->
                <div>
                    <div>
                        <table class="ta3334">
                            <thead style="border-top:2px solid #9448e6 ">
                                <tr>
                                    <th style="width: 100px;"></th>
                                    <td style="width: 50px;">{{ MjVo.idx }}</td>
                                    <th style="width: 100px;"></th>
                                    <td style="width: 120px;">{{ MjVo.regDate }}</td>
                                    <th style="width: 80px;"></th>
                                    <td style="width: 80px;">{{ MjVo.pagesNo }}</td>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <th></th>
                                    <td colspan="3">{{ MjVo.title }}</td>
                                    <th></th>
                                    <td>{{ MjVo.notice_name }}</td>
                                </tr>
                                <tr>
                                    <td colspan="6" v-html="subTitle">
                                        
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                        <div class="table">
                            <button v-if="this.no==3" id="notice_btn" @click="goDelete"></button>
                            <button id="notice_btn" @click="goList"></button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!--  -->
        <AppFooter />
    </div>
</template>

<script>
import '@/assets/css/Initialization.css';
import '@/assets/css/mj/read.css';
import AppFooter from '@/components/AppFooter.vue';
import AppHeader from '@/components/AppHeader.vue';
import axios from 'axios';

export default {
    name: "ReadView",
    components: {
        AppHeader,
        AppFooter,
    },
    data() {
        return {
            MjVo: {
                idx:'',
                title:'',
                notice_name:'',
                content:'',
                regDate:'',
                pagesNo:''
            },
            no:""
        };
    },
    computed: {
        subTitle() {return this.MjVo.content.replaceAll("\n", "<br />")}
	},
    methods: {
        fetchNotice() {
            axios({
                method: 'get', // put, post, delete                   
                url: `${this.$store.state.apiBaseUrl}/odo/mypage/notice/${this.$route.params.no}`,
                headers: { 'Content-Type': 'application/json; charset=utf-8',
								'Authorization' : 'Bearer ' + this.$store.state.token}, //
                //params: guestbookVo, //get   
                // data: guestbookVo, //put, post, delete   JSON  

                responseType: 'json' //
            }).then(response => {
                // console.log(response); //

               this.MjVo=response.data.MjVo;
               this.no=response.data.num;

            }).catch(error => {
                console.log(error);
                
            });
        },
        goDelete() {
            axios({
                method: 'delete', // put, post, delete                   
                url: `${this.$store.state.apiBaseUrl}/odo/mypage/notice/delete/${this.$route.params.no}`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                //params: guestbookVo, //get   
                //data: guestbookVo, //put, post, delete   JSON  

                responseType: 'json' //
                /* eslint-disable */
            }).then(response => {
                // console.log(response); //
                this.$router.push('/mypage/notice'); //  ,   

            }).catch(error => {
                console.log(error);
                
            });
        },
        goList() {
            //     
            this.$router.push('/mypage/notice'); //  ,   
        }
    },
    created() {
        this.fetchNotice(); //    fetchNotice  
    }
};
</script>

<style>
.table{
    margin-left: 50px;
}
</style>