<template>
	<div>
		<!-- AppHeader -->
		<AppHeader />
		<div class="wrap">
			
			<!-- container  -->
			<div class="MporderContainer">
				<div class="MpLeft">
					
					<!-- nav -->
					<!-- <MyPageSide /> -->

				</div>
				<!--  -->
				<div class="MpRight">
					
						<div id="in_title">
							<textarea v-model="write.title" name="title" id="utitle" rows="1" cols="55" placeholder="" maxlength="100"></textarea>
						</div>
						<!-- <div class="wi_line"></div> -->
						<div id="in_content">
							<textarea v-model="write.content" name="content" id="ucontent" placeholder=""></textarea>
						</div>
						<div class="bt_se">
							<button id="sign" type="submit" v-on:click.prevent="goList"></button>
						</div>
					
				</div>
			</div>
		</div>
	</div>
		<!--  -->
		<AppFooter/>
</template>

<script>
import '@/assets/css/Initialization.css'
import "@/assets/css/mj/write.css"
import AppFooter from '@/components/AppFooter.vue';
import AppHeader from '@/components/AppHeader.vue';
// import MyPageSide from '@/components/MyPageSide.vue';
import axios from 'axios';

	export default {
		name: "ModifyView",
		components: {
			AppHeader,
			AppFooter,
			// MyPageSide
		},
		data() {
			return {
				write:{
					title:"",
					content:""

				}
			};
		},
		methods: {
			
			goList() {
				// console.log("");
				// console.log(this.write);
				axios({
                method: 'post', // put, post, delete                   
                url: `${this.$store.state.apiBaseUrl}/odo/mypage/notice/write`,
                headers: { 'Content-Type': 'application/json; charset=utf-8',
								'Authorization' : 'Bearer ' + this.$store.state.token }, //
                //params: guestbookVo, //get   
                data: this.write, //put, post, delete   JSON  

                responseType: 'json' //
				/* eslint-disable */
            }).then(response => {
                // console.log(response); //
                this.$router.push('/mypage/notice'); //  ,   

            }).catch(error => {
                console.log(error);
                
            });
        }
		},
		created(){}
	};
</script>

